<template>
    <div class="signUpLoginBox">
        <div class="slContainer">
            <div class="formBoxLeftLogin">
            </div>
            <div class="formBoxRight">
                <div class="formContent">

                    <span v-if="errors.msge">{{ errors.msge }}</span>

                    <h2>Connexion</h2>
                    <form @submit.prevent="login">

                        <div class="inputBox">
                            <input type="email" id="email" autocomplete="off" @blur="checkEmail" v-model="form.email" :class="{'filled': form.email !== ''}" required />
                            <label for="email">Email</label>
                        </div>

                        <div class="inputBox">
                            <input type="password" id="password" autocomplete="off" @blur="checkEmail" v-model="form.pwd" :class="{'filled': form.pwd !== ''}" required />
                            <label htmlFor="password">Mot de passe</label>
                        </div>

                        <button v-if="form.email !== '' && form.pwd !== ''">Connexion</button>
                        <button disabled v-else>Connexion</button>

                    </form>
                    <div class="linkContainer">
                        <Link class="simpleLink" to="/signup">Nouveau sur Marvel Quiz ? Inscrivez-vous maintenant.</Link>
                        <br />
                        <Link class="simpleLink" to="/forgetpassword">Mot de passe oubli? Rcuprez-le ici.</Link>
                    </div>
                </div>
            </div>
        </div>
        <Loading :loaded="!isLoading"/>
    </div>
</template>

<script>
    import firebase from 'firebase'
    import Loading from "../../components/Loading";

    export default {
        name: "Login",
        components: {Loading},
        data: () => {
            return {
                form: {
                    email: '',
                    pwd: ''
                },
                errors: {},
                isLoading: false
            }
        },
        methods: {
            login: function () {
                this.isLoading = true
                firebase
                    .auth()
                    .signInWithEmailAndPassword(this.form.email, this.form.pwd)
                    .then((user) => {
                        this.isLoading = false
                        this.$router.push('/quiz')
                    })
                    .catch(error => {
                        this.isLoading = false
                        this.errors.msge = error.message
                    });
            },
        }
    }
</script>

