import firebase from "firebase";

export default function auth () {
    console.log('authe .....')
    let user = null
    firebase.auth().onAuthStateChanged((res) => {
        user = res
    })

    if (user) {
        console.log('user connected: ', user)
        return true
    }
    else {
        console.log('user not connected: ', user)
        return false
    }
}