<template>
    <div class="logoutContainer">
        <label class="switch" title="LogOut">
            <input type="checkbox" :checked="isLogout" v-model="isLogout" @change="logout" title="LogOut"/>
            <span class="slider round" data-tip="Dconnexion"></span>
        </label>
    </div>
</template>

<script>
    import firebase from 'firebase'
    export default {
        name: "Logout",
        data: () => {
            return {
                isLogout: false
            }
        },
        methods: {
            logout: function () {
               if (this.isLogout) {
                   console.log('deconnexion ....')
                   firebase
                       .auth()
                       .signOut()
                       .then(() => {
                           // alert('Successfully logged out');
                           this.$router.push('/');
                       })
                       .catch(error => {
                           // alert(error.message);
                           // this.$router.push('/');
                       });
               }else {
                   console.log('impossible de se deconnecter')
               }
            },
        }
    }
</script>

<style scoped>
    .logoutContainer {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 30px;
    }

    .switch {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 34px;
    }

    .switch input {
        opacity: 0;
        width: 0;
        height: 0;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: var(--light-grey-color);
        -webkit-transition: .4s;
        transition: .4s;
    }

    .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }

    input:checked+.slider {
        background-color: var(--red-color);
    }

    input:checked+.slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }

    .slider.round {
        border-radius: 34px;
    }

    .slider.round:before {
        border-radius: 50%;
    }
</style>