<template>
    <div class="signUpLoginBox">
        <div class="slContainer">
            <div class="formBoxLeftSignup">
            </div>
            <div class="formBoxRight">
                <div class="formContent">

                    <h2>Inscription</h2>
                    <form @submit.prevent="save">

                        <div>
                            <span v-if="errors.msge" style="margin-bottom: 30px !important;">
                                {{ errors.msge }}
                                <router-link to="/login" style="color: #fff;">SignIn</router-link>
                            </span>

                        </div>

                        <div class="inputBox">
                            <input type="text" id="pseudo" v-model="form.pseudo" :class="{'filled': form.pseudo !== ''}" @change="pseudoCheck" required />
                            <label for="pseudo">Pseudo</label>
                            <span v-if="errors.pseudo" class="error">{{ errors.pseudo }}</span>
                        </div>

                        <div class="inputBox">
                            <input type="email" name="myemail" id="email" v-model="form.email" :class="{'filled': form.email !== ''}" @change="emailCheck" required />
                            <label for="email">Email</label>
                            <span v-if="errors.email" class="error">{{ errors.email }}</span>
                        </div>

                        <div class="inputBox">
                            <input type="password" id="password" name="pwd" v-model="form.pwd" autocomplete="off" :class="{'filled': form.pwd !== ''}" @change="pwdLength" required />
                            <label for="password">Mot de passe</label>
                            <span v-if="errors.pwd" class="error">{{ errors.pwd }}</span>
                        </div>

                        <div class="inputBox">
                            <input type="password" id="confirmPassword" autocomplete="off" v-model="form.confirmPwd" :class="{'filled': form.confirmPwd !== ''}" @change="pwdMatch" required />
                            <label for="confirmPassword">Confirmer le mot de passe</label>
                            <span v-if="errors.confirmPwd" class="error">{{ errors.confirmPwd }}</span>
                        </div>

                        <button type="submit" disabled v-if="form.pseudo === '' || form.email === '' || form.pwd === '' || form.confirmPwd === '' || form.pwd !== form.confirmPwd">Register</button>
                        <button type="submit" v-else>Register</button>

                    </form>
                    <div class="linkContainer">
                        <router-link class="simpleLink" to="/login">I already have acount? Sign In.</router-link>
                    </div>
                </div>
            </div>
        </div>
        <Loading :loaded="!isLoading"/>
    </div>
</template>

<script>

    import firebase from 'firebase'
    import Loading from "../../components/Loading";

    export default {
        name: "Register",
        components: {Loading},
        data: () => {
            return {
                form: {
                    pseudo: '',
                    email: '',
                    pwd: '',
                    confirmPwd: ''
                },
                errors: {},
                isLoading: false
            }
        },
        methods: {
            save: function () {
                if (this.checkForm()) {
                    this.isLoading = true
                    firebase
                        .auth()
                        .createUserWithEmailAndPassword(this.form.email, this.form.pwd)
                    .then((user) => {

                        var db = firebase.firestore()
                        db.collection("users").doc(user.user.uid).set({
                            pseudo: this.form.pseudo,
                            email: this.form.email
                        }).then(() => {
                            this.form.pseudo = ''
                            this.form.email = ''
                            this.form.pwd = ''
                            this.form.confirmPwd = ''
                        }).catch((error) => {
                            this.isLoading = false
                            console.error("Error writing document: ", error);
                        });
                        this.isLoading = false
                        this.$router.push('/quiz');
                    })
                        .catch(error => {
                            this.isLoading = false
                            this.errors.msge = error.message
                        });
                }else {
                    console.log('error')
                }
            },
            checkForm: function () {
                let isValid = true
                this.errors = {}
                if (!this.pseudoCheck()) {
                    this.errors.pseudo = 'Please pseudo field is required !'
                    isValid = false
                }
                if (!this.emailCheck()) {
                    this.errors.email = 'Please email field is required !'
                    isValid = false
                }
                if (this.form.pwd === '' || this.form.pwd === null) {
                    this.errors.email = 'Please password field is required !'
                    isValid = false
                }
                if (this.form.confirmPwd === '' || this.form.confirmPwd === null) {
                    this.errors.email = 'Please confirm your password !'
                    isValid = false
                }else {
                    if (!this.pwdMatch())
                        isValid = false
                }

                return isValid
            },
            pwdLength: function () {
                this.errors.pwd = ''
                if (this.pwdMatch() && this.form.pwd.length >= 6)
                    return true
                if (this.form.pwd.length < 6) {
                    this.errors.pwd = 'Your password should be more than 6 charaters !'
                    return false
                }
                return true
            },
            pwdMatch: function () {
                this.errors.pwd = ''
                this.errors.confirmPwd = ''
                let isValid = true
                if (this.form.pwd === '' || this.form.pwd === null) {
                    this.errors.pwd = 'Please password field is required !'
                    isValid = false
                }

                if (this.form.pwd !== this.form.confirmPwd) {
                    this.errors.confirmPwd = 'Passord doesn\'t match !'
                    isValid = false
                }

                return isValid
            },
            pseudoCheck: function () {
                this.errors.pseudo = ''
                if (this.form.pseudo === '' || this.form.pseudo === null) {
                    this.errors.pseudo = 'Please pseudo field is required !'
                    return false
                }
                return true
            },
            emailCheck: function () {
                this.errors.email = ''
                if (this.form.email === '' || this.form.email === null) {
                    this.errors.email = 'Please email field is required !'
                    return false
                }
                return true
            },
        },
        computed: {
            checkError() {
                if (this.errors.pseudo !== '' && this.errors.email !== '' && this.errors.pwd == '' &&
                    this.errors.confirmPwd && this.errors.pwd === this.errors.confirmPwd)
                    return true

                return false
            }
        }
    }
</script>

<style scoped>
    .simpleLink {
        color: var(--black-color);
        text-decoration: none;
        font-size: 14px;
    }

    .simpleLink:hover {
        color: var(--red-color);
    }
    .error {
        border: none !important;
        margin: 4px 0 !important;
        position: relative;
        top: -35px;
        font-size: 12px;
    }
</style>