import firebase from "firebase";

export default {
    namespaced: true,
    state: {
        token: null,
        user: null
    },
    getters: {
        user (state) {
            return state.user
        },
        token (state) {
            return state.token
        }
    },
    mutations: {
        SET_TOKEN (state, token) {
            state.token = token
        },
        SET_USER (state, data) {
            state.user = data
        }
    },
    actions: {
        async signIn({ dispatch }, credentials){
            // let token = null

           let respone = await firebase
                .auth()
                .signInWithEmailAndPassword(credentials.email, credentials.pwd)

            console.log('response :', respone)
            return dispatch('attempt', respone.refreshToken)
        },
        async attempt({ commit }, token) {
            commit('SET_TOKEN', token)

            try {
                // let tmp = null
                firebase.auth().onAuthStateChanged((user) => {
                    if (user) {
                        // tmp = user;
                        commit('SET_USER', user)
                    } else {
                    }
                });
                // commit('SET_USER', tmp)
            }catch (e){
                commit('SET_TOKEN', null)
                commit('SET_USER', null)
            }

            return true
        },
        signOut ({ commit }, credentials) {
            return axios.post('auth/deconnexion', credentials).then(() => {
                commit('SET_TOKEN', null)
                commit('SET_USER', null)
                commit('SET_CONNEXION', null)
            })
        }
    }
}