<template>
    <div class="welcome-page">
        <div class="content">
            <Logout/>
            <QuizComponent/>
        </div>
    </div>
</template>

<script>
    import firebase from 'firebase'
    import Logout from "../components/Logout";
    import QuizComponent from "../components/Quiz";

    import { Quiz } from '../modules/modules'

    import Swal from 'sweetalert2'

    const Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
    })

    export default {
        name: "WelcomePage",
        components: {QuizComponent, Logout},
        data: () => {
            return {
                quiz: new Quiz(),
                data: {},
                user: {}
            }
        },
        mounted() {
            // console.log('welcom : ', firebase.auth().currentUser)
            const db = firebase.firestore()
            let users = null

            const currentUser = firebase.auth().currentUser;

            db.collection('users').doc(currentUser.uid).get()
            .then((res) => {
                if (res.exists) {
                    this.user = res.data()
                    Toast.fire({
                        icon: 'info',
                        title: `Welcom <span style="font-weight: bold; text-transform: uppercase; color: #4f78a4;">${this.user.pseudo}</span> to CS Quiz. Good luck`
                    })
                }
                // console.log('user collections: ', res)
            })
        },
        methods: {

        }
    }
</script>

<style scoped>

    .welcome-page {
        margin: 0;
        padding: 0;
        background-color: var(--white-color);
        display: flex;
        justify-content: center;
    }

    .content {
        background-color: var(--white-color);
        padding: 30px;
        flex: 0 1 80%;
        display: flex;
        flex-direction: column;
        align-items: stretch;
    }

    .bienvenu {
        text-transform: uppercase;
        font-weight: bold;
    }

</style>